import React from 'react';
import './Order.css';

const Order = ({ name, price, createdAt }) => (
  <div className="order">
    <div className="order__upper">
      <p className="p--order">: {name}</p>
      <p className="p--order">
        : <span className="order-price">{price}</span>
      </p>
    </div>
    <div className="order__lower">
      <p className="p--order">: {createdAt}</p>
    </div>
  </div>
);

export default Order;



